import axios from 'axios'
import { Message, MessageBox } from 'element-ui'

const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: '',
};

// eslint-disable-next-line
function $params(obj) {
  const str = [];
  for (const p in obj) {
    str.push(
      encodeURIComponent(p) +
      '=' +
      encodeURIComponent(typeof obj[p] === 'object' ? JSON.stringify(obj[p]) : obj[p])
    );
  }
  return str.join('&');
}
/**
 * @description: axios
 * @param options :{methods:,url,headers,data,type}
 * @return: promise
 */
export default function request( options, timeout = 10000) {
  return new Promise((resolve, reject) => {
    const headers = {};
    let url = options.url;
    // if (process.env.NODE_ENV === 'production'){
      url = url.replace(/^\/api/,'').replace(/^\/mock/,'');
      url = 'http://sale-game.haoyunqi.com.cn' + url;
    // }
    // console.log(process.env.NODE_ENV ,url);
    const data = {
      method: options.method || 'GET',
      url,
      headers: {
        Accept: 'application/json',
        'Content-Type': options.type === 'json' ? 'application/json; charset=utf-8':'application/x-www-form-urlencoded',
        ...options.headers,
        ...headers
      },
      timeout:timeout
    };
    switch (data.method) {
      case "GET":
        data.params = options.data;
        break;
      case "POST":
        // data.data = $params(options.data);
        data.data = options.type === 'json' ? options.data : $params(options.data);
        break;
    }
    axios(data).then((resp) => {
      if(resp.data.status === 200 || resp.data.code == '200' ){
        resolve(resp.data);
      }else{
        reject(resp);
        if(resp.data){
          Message({
            message: resp.data.message || '',
            type: 'error',
            duration: 5 * 1000
          })
        }
      }
    }).catch((err) => {
      console.dir(err);
      reject(err);
      if(err.response) {
        if(err.response.status === 401){
          // Toast.info('');
          MessageBox.confirm(
            '',
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }
          )
          return
        }
        const errortext = codeMessage[err.response.status] || ',';
        Message({
          message: errortext,
          type: 'error',
          duration: 5 * 1000
        })
      }else{
        Message({
          message: ',',
          type: 'error',
          duration: 5 * 1000
        })
      }
    })
  });
}

